export default {
	data() {
		return {
			user:{}
		};
	},
	onShow(){
		var user = {};
		uni.getStorage({
		    key: 'user',
		    success: function (res) {
				user = res.data[0];
				console.log(user);
		    }
		});
		this.user = user;
	},
	methods: {
		showType(tbIndex){
			this.tabbarIndex = tbIndex;
			this.list = this.orderList[tbIndex];
		},
		toChangeInfo() {
			uni.navigateTo({
				url:'changeinfo'
			})
		}
	}
}
